<template lang="html">
  <div class="orderList">
    <div class="app-layout-flex">
      <div class="app-layout-flex-left">
        <span>订单列表</span>
        <el-tooltip class="item" effect="dark" content="查看所有订单" placement="right">
          <i class="el-icon-question"></i>
        </el-tooltip>
      </div>
      <div class="app-layout-flex-right">
        <el-button type="primary" @click="toOrderCreate">新建订单</el-button>
      </div>
    </div>
    <el-table
      :data="orderList"
      border
      stripe
      style="width: 100%"
      :default-sort="{ prop: 'data', order: 'descending' }"
      >
      <el-table-column
        prop="orderId"
        label="订单号"
        align="center"
        width="120"
        sortable>
      </el-table-column>
      <el-table-column
        prop="orderTime"
        label="订单时间"
        align="center"
        sortable
        width="120">
      </el-table-column>
      <el-table-column
        prop="productName"
        label="产品"
        align="center"
        width="150">
      </el-table-column>
      <el-table-column
        prop="staffName"
        label="员工"
        align="center">
      </el-table-column>
      <el-table-column
        prop="customerName"
        label="客户"
        align="center">
      </el-table-column>
      <el-table-column
        label="订单状态"
        align="center">
        <template slot-scope="scope">
          {{ scope.row.state }}
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center" width="300">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="toOrderDetail(scope.$index, scope.row)">查看</el-button>
          <el-button
            size="mini"
            @click="toOrderEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button
            size="mini"
            type="danger"
            @click="deleteOrder(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<style lang="scss" scoped>
.orderList {
  min-height: 500px;
  padding: 20px;
  border: 1px solid #ddd;

  .app {
    &-layout-flex {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 20px;

      &-left {
        font-size: 20px;
        font-weight: bold;

        .el-icon-question {
          margin-left: 10px;
        }
      }
    }
  }
}
</style>

<script>
import { mapState, mapMutations, mapActions } from 'vuex'

// import ImsClient from './lib/ImsClient'

export default {

  name: 'OrderList',
  computed: {
    ...mapState(['orderList']),
  },
  methods: {
    ...mapMutations(['setOrderList']),
    ...mapActions(['getOrderList']),
    toOrderCreate () {
      this.$router.push({
        name: 'OrderCreate',
      })
    },
    toOrderDetail (index, row) {
      this.$router.push({
        name: 'OrderDetail',
        query: {
          orderId: row.orderId,
        },
      })
    },
    toOrderEdit (index, row) {
      this.$router.push({
        name: 'OrderEdit',
        query: {
          orderId: row.orderId,
        },
      })
    },
    deleteOrder (index, row) {
      this.$confirm(`此操作将永久删除订单${row.orderId}，是否继续？`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      }).then(() => {
        const orderList = this.orderList.filter((v, i) => i !== index)

        this.setOrderList(orderList)

        this.$message({
          message: '删除成功!',
          type: 'success',
        })
      }).catch(() => {
        this.$message({
          message: '已取消删除',
          type: 'info',
        })
      })
    },
  },
  created () {
    this.getOrderList()

    // ImsClient()
  },

}
</script>
